/* Стилі всієї картки */
.card {
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
  margin: 0 24px 25px;
  padding-top: 12px;

  /* Стилі картинки */
  &__img {
    width: 160px;
    // height: 134px;
    margin-bottom: 41px;
    border: 3px solid transparent;
    transition: transform 1s;
    &:hover {
      transform: scale(1.25);
      border-color: #f3f3f3;
    }
  }

  /* Стилі інформації під картинкою */
  &__info {
    padding: 0 12px 15px;
    font-weight: 400;
    font-size: 11px;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    margin-top: 0;
  }

  &__stars-feedback,
  &__price {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 15px;
  }
  
  &__stars-feedback,
  &__price-value {
    text-align: right;
  }

  &_code {
    margin-bottom: 15px;
  }
  
  /* Стилізація ціни */
  &__price-value {
    font-size: 16px;
    font-weight: 700;
  }
}

.box {
  padding: 0 19px;
}

/* Стилізація зірок */
.stars {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;

  @for $i from 1 through 5 {
    &--#{$i} :nth-child(-n + #{$i}) {
      background-image: url("../images/star-active.svg");
    }
  }

  &__star {
    width: 15px;
    height: 15px;
    background-image: url("../images/star.svg");
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;
  }

}

/* Стилізація кнопки */
.card__btn {
  width: 170px;
  height: 40px;
  display: grid;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  font-weight: bold;
  font-size: 14px;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #00acdc;

  &:hover {
    color: #00acdc;
    background-color: #fff;
    border: 1px solid #00acdc;
    transition: 0.5s;
  }
}
